import { SIDEBARMENU } from "../../lib/constants";

export const Sidebar = () => {
  

  return (
    <div className="w-64 h-full p-4 fixed z-10">
      <div className="flex h-full bg-primary flex-col rounded-xl p-3">
        <div className="flex items-center mb-10 gap-3">
          <img src="/logo.webp" alt="Logo" className="w-9" />
          <h2 className="text-xl font-bold text-white">NexusFlow</h2>
        </div>
        <div className="flex flex-col gap-5">
          {SIDEBARMENU.map((item) => (
            <div key={item.name} className="cursor-pointer">
              <a href={item.route} className="text-white text-medium">
                {item.name}
              </a>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
